.thanks-outer {
  padding: 5px;
}

.t-head {
  color: #3e3e3e;
  font-size: 21px;
  text-align: center;
  padding-top: 0px;
  padding-bottom: 20px;
  font-family: "GothamNarrowBold", sans-serif;
  line-height: 48px;
}

.thanks-outer {
  margin: 0 auto;
  padding-top: 0px;
  margin-bottom: 30px;
}

.icon-outer span {
  min-width: 56px;
  text-align: center;
  cursor: pointer;
}

.outer {
  height: 93vh;
  margin-top: 70px;
  align-items: center;
  display: flex;
  justify-content: center;
}

.bg {
  background: url("../../../assets/img/background.jpg");
  background-size: cover;
  height: 100vh;
}

.card {
  max-width: 777px;
  background-color: rgba(255, 255, 255, 0.8);
  height: auto;
  border-radius: 40px;
  width: 548px;
  box-shadow: 5px 6px 17px 0 rgba(0, 0, 0, 0.11);
  overflow: auto;
}

.card-body {
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  min-height: 1px;
  padding: 2.25rem;
}

.moose-head {
  font-size: 24px;

  background-color: #eaeaea;
  text-align: center;
  color: #f26323;
  padding: 10px 35px;
  border-radius: 13px;
}

.card-body {
  padding: 40px;
}

.spacer {
  width: calc(100% + 68px);
  margin-top: 22px;
  margin-bottom: 19px;
}

.reg-form {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-between;
}

.mat-divider {
  border-top-color: rgba(0, 0, 0, 0.05);
}